import React, { useState } from 'react';

const Pagination = ({onChange, items, itemsPerPage}) => {

    const [currentPage, setCurrentPage] = useState(1)

    const pagesCount = Math.ceil(items / itemsPerPage);
    
    // const pages = Array(pagesCount);

    const pages = [];

    for(let i=1; i<= pagesCount; i++) {
        pages.push(i)
    }

    const handleClick = (page) => {
        setCurrentPage(page);

        const end = page * itemsPerPage;
        const start = end - itemsPerPage; 

        onChange(start, end, page);
    };

    return (
        <div>
            <ul className="pagination pagination-lg">
                <li className={"page-item" + (currentPage === 1 ? "disabled" : "")}>
                    <button className="btn page-link" onClick={() => handleClick(currentPage - 1)}>&laquo;</button>
                </li>
                {pages.map((p) => (
                <li className={"page-item" + (p === currentPage ? "active" : "")}> 
                    <button className="btn page-link"  onClick={() => handleClick(p)}> {p}  </button>
                </li>
                ))}     
                <li className="page-item">
                    <button className="btn page-link" >2</button>
                </li>
                <li className="page-item">
                    <button className="btn page-link" >3</button>
                </li>
                <li className="page-item">
                    <button className="btn page-link" >4</button>
                </li>
                <li className="page-item">
                    <button className="btn page-link" >5</button>
                </li>
                <li className="page-item">
                    <button className="btn page-link" onClick={() => handleClick(currentPage + 1)}>&raquo;</button>
                </li>
            </ul>
        </div>
    )
}

export default Pagination;