import React, {useState} from "react";
import { Link } from 'react-router-dom';
import Pagination from '../components/Pagination';



const CustomersPage = ({customers, onDelete}) => {

  const [pagination, setPagination] = useState({
    start:0,
    end:5,
  });

  const [search, setSearch] = useState("");

  const handleSearch = ({target}) => {
    setSearch(target.value);
  };

  const handlePageChange = (start, end, page) => {
    setPagination({
      start: start,
      end: end,
    });
  };

  const filteredCustomers = customers.filter(function(c) {
    return c.firstName.toLowerCase().includes(search.toLowerCase()) || 
    c.lastName.toLowerCase().includes(search.toLowerCase()) || 
    c.email.toLowerCase().includes(search.toLowerCase()) 
  });

  return (
      <div>
        <h1>Liste des clients</h1>
        <Link to="/customers/create" className="btn btn-primary">
          Ajouter un client
        </Link>
        <input
          type="text"
          placeholder="Rechercher..."
          className="form-control"
          value={search}
          onChange={handleSearch}
        />
        <table className="table table-hover">
          <thead>
            <tr>
              <th>Numéro</th>
              <th>Client</th>
              <th>Email</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {customers.slice(pagination.start, pagination.end).map((c) => (
              <tr key={c.id}>
                <td>{c.id}</td>
                <td>{c.firstName} {c.lastName}</td>
                <td>{c.email}</td>
                <td>
                <button onClick={() => onDelete(c.id)}
                  className="btn btn-sm btn-danger">X</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        <Pagination items={filteredCustomers.length} itemsPerPage={5} onChange={handlePageChange} />
      </div>
    );
}

export default CustomersPage;