import "bootswatch/dist/lumen/bootstrap.min.css";
import "./App.css";
import { useState } from "react";

import { BrowserRouter, Switch, Route } from "react-router-dom";
import CustomersPage from "./pages/CustomersPage";
import CustomerFormPage from "./pages/CustomerFormPage";
import CUSTOMERS from "./Customers";
import toast, { Toaster } from 'react-hot-toast';

const notifyAjt = () => toast(`C'est fait`)
const notifySup = () => toast(`C'est supprimé`)

function App() {
  const [customers, setCustomers] = useState(CUSTOMERS);

  const deleteCustomer = (id) => {
    const updateCustomers = customers.filter((c) => c.id !== id);
    setCustomers(updateCustomers);
  };

  const createCustomer = (customer) => {
    // creer client
    // customer.id = Date.now;
    customer.id = Math.max(...customers.map((c) => c.id)) + 1;

    // ajouter Ã  la liste
    setCustomers([...customers, customer]);
  };

  return (
    <BrowserRouter>
      <h1>Gestion clients</h1>

      <Switch>
        <Route path="/customers/create">
          <CustomerFormPage onCreate={createCustomer} />
        </Route>
        <Route path="/">
          <CustomersPage customers={customers} onDelete={deleteCustomer} />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}

export default App;